import React from 'react';
import ScrollSVG from "./Scroll.svg"
import "./Scroll.scss"
import Arrow from "./triangle1.png"
const Scroll = () => {
    return (
        <div class="Scroll">
            <img className="stripes" src={ScrollSVG} alt="Scroll"/>
            <div className="glitch-wrapper">
					<h2 data-text="Scroll" className="glitch-text">
						SCROLL
					</h2>
			</div>
            <img className="triangle" src={Arrow} alt=""/>
        </div>
    );
};

export default Scroll;